@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");

/*Set the variable*/
:root {
  --primary-white: #ffffff;
  --primary-skyblue: #65c6f0;
  --primary-dark: rgb(52, 59, 68);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-trasparent-black: rgba(0, 0, 0, 0.7);
  --primary-green: green;
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/*********************  NAVBAR *********************/
nav {
  z-index: 2;
}
.logo {
  width: 16.25rem;
}

/*********************  MUNU LINKS *********************/
nav a.nav-link {
  font-size: 0.8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

var ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}
@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.1875rem solid var(--primary-skyblue);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid var(--primary-skyblue);
}

/*********************  HEADER *********************/
.header-wraper {
  position: relative;
  background: url(../src/Images/headerImage.png) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: var(--primary-skyblue);
  /* text-transform: uppercase; */
}

/*********************  TYPED TEXT *********************/

.typed-text {
  font-size: 2rem;
  color: var(--primary-white);
}

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.5rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
    color: var(--primary-white);
  }
}

/*********************  HEADER MAIN OFFER BUTTON *********************/

.btn-main-offer {
  border: 1px solid var(--primary-skyblue);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: var(--primary-white);
  margin: 2rem;
}
.btn-main-offer:hover {
  text-decoration: none;
  background-color: var(--primary-skyblue);
  color: var(--primary-white);
  transition: 0.25 ease-in-out;
}

/*********************  PARTICLES JS *********************/

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/*********************  ABOUT ME *********************/

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile-img {
  width: 30rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid var(--primary-white);
}

.about-heading {
  color: var(--primary-skyblue);
  text-transform: uppercase;
}
@media (max-width: 468px) {
  .profile-img {
    width: 15rem;
  }
}

/*********************  Skills *********************/
.skills {
  text-align: center;
  padding-bottom: 2rem;
  background-color: var(--primary-light-grey);
}

.skills h1 {
  color: var(--primary-skyblue);
  text-transform: uppercase;
}

.skills .circle {
  position: relative;
  margin: 0.375rem auto;
  background: var(--primary-skyblue);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.skills .icon {
  font-size: 1.4rem;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.skills .box {
  height: 16rem;
  border-bottom: 5px solid var(--primary-skyblue);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: white;
  transition: 0.3s ease-in-out;
}

.skills .box:hover {
  background: var(--primary-skyblue);
  border-bottom: 5px solid var(--primary-dark);
}

.skills .box:hover .icon {
  color: var(--primary-dark);
}

.skills .box:hover .circle {
  background: var(--primary-white);
}

.skills .box:hover p {
  color: var(--primary-white);
}

ul.horizontal-slide {
  margin: 0;
  padding: 0;
  width: 100%;
  white-space: nowrap;
  overflow-x: auto;
}
ul.horizontal-slide li[class*="col"] {
  display: inline-block;
  float: none;
}

ul.horizontal-slide li[class*="col"]:first-child {
  margin-left: 0;
}

/*********************  Milestones *********************/
.milestones {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.milestones h1 {
  text-transform: uppercase;
  color: var(--primary-skyblue);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}
.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.2rem solid var(--primary-dark);
  background: var(--primary-skyblue);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: var(--primary-red);
  font-weight: 900;
}

/*********************  MEDIA *********************/
@media (max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }
  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

/*********************  PORTFOLIO *********************/
.porfolio-wrapper {
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.porfolio-wrapper h1 {
  color: var(--primary-skyblue);
}
.portfolio-img {
  width: 15rem;
  height: 9rem;
  border: 1px solid var(--primary-skyblue);
  padding: 0.3rem 0.2rem;
}
.portfolio-img-box {
  position: relative;
  margin: 1rem;
}

/*********************  OVERFLOW BOX *********************/
.overflow {
  position: absolute;
  opacity: 0;
  background: var(--primary-dark);
  width: 15rem;
  height: 9rem;
  top: 0;

  cursor: pointer;
}

.portfolio-img-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -9rem;
  margin-top: 3rem;
  color: var(--primary-skyblue);
  font-size: 2.5rem;
}

.portfolio-img-box:hover .portfolio-icon {
  opacity: 100%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
.popupbox-wrapper {
  margin-top: 8rem;
}

/*********************  POPUPBOX *********************/
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}
.hyper-link {
  cursor: pointer;
  color: var(--primary-skyblue);
}
.hyper-link:hover {
  color: var(--primary-red);
}

/*********************  POPUPBOX MEDIA*********************/
@media (max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media (max-height: 570px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 60%;
  }
}

@media (max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
  .portfolio-image-popupbox {
    width: 40%;
  }
}

/*********************  TESTIMONIALS *********************/
.testimonials {
  background-image: url(./Images/testimonial-bg.jpg);
  background-size: cover;
  background-color: var(--primary-dark);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}
.testimonials h1 {
  color: var(--primary-skyblue);
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonial-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background-color: var(--primary-trasparent-black);
}

.myCarousel {
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 20rem;
}

.myCarousel h3 {
  font-weight: 600;
  text-transform: uppercase;
  color: var(--primary-skyblue);
}

.myCarousel p {
  color: var(--primary-grey);
  font-size: 20px;
}

.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

/* Iphone X */
@media only screen and (device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
  .myCarousel {
    height: 35rem;
  }
}
/* Iphone 8 */
@media only screen and (device-width: 375px) and (device-height: 667px) and (-webkit-device-pixel-ratio: 2) {
  .myCarousel {
    height: 35rem;
  }
}
/* Iphone 8 plus */
@media only screen and (device-width: 414px) and (device-height: 736px) and (-webkit-device-pixel-ratio: 3) {
  .myCarousel {
    height: 35rem;
  }
}
/*********************  CONTACT ME *********************/

.contacts {
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: var(--primary-skyblue);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}
/*********************  INPUT *********************/
.contacts input,
.contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: var(--primary-skyblue);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus,
.contacts textarea:focus {
  background: transparent;
  color: var(--primary-skyblue);
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.1rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}
.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: var(--primary-skyblue);
  display: inline-block;
}

input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}
textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: var(--primary-red);
  text-align: left;
}

.success-message {
  font-weight: bold;
  color: var(--primary-skyblue);
}

/*********************  MEDIA *********************/
@media (max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/*********************  FOOTER *********************/

.footer {
  background: var(--primary-trasparent-black);
  color: var(--primary-light-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: var(--primary-light-grey);
}

.footer a:hover {
  text-decoration: none;
  color: var(--primary-skyblue);
  cursor: pointer;
}

.footer p {
  padding-bottom: 0;
}
